<template>
<transition name="slide-fade">
	<div class="cc-main">
		<div class="cc-edit flexend" @click="goto('editcompany')">
			<span>编辑</span>
			<img class="cc-logo" src="../assets/img/edit.png" alt="">
		</div>
		<div class="cc-name">{{detail.name}}</div>
		<div class="cc-content">{{detail.sizeStr}} · {{detail.industry}}</div>
		<div class="cc-title">公司介绍</div>
		<div class="cc-content">{{detail.description}}</div>
		<div class="cc-title">公司照片</div>
		<div class="cc-pic">
			<van-swipe class="my-swipe" :loop="false" :width="212" :show-indicators="false">
				<van-swipe-item v-for="(image, index) in images" :key="index"><div class="cc-item"><van-image width="200" height="124" fit="cover" :src="image" /></div></van-swipe-item>
			</van-swipe>
		</div>
		<div class="cc-title">公司地址</div>
		<div class="cc-content">{{detail.city}}  {{detail.address}}</div>
		<div class="cc-title">企业联系人</div>
		<div class="cc-content">{{detail.linkman}} {{detail.phone}}</div>
	</div>
</transition>
</template>
<script type="text/javascript">
import {Swipe, SwipeItem,Toast,Image } from 'vant';
export default {
	name: 'home',
	components: {
		[Swipe .name]: Swipe ,
		[SwipeItem.name]: SwipeItem,
		[Toast.name]: Toast,
		[Image.name]: Image,
	},
	data() {
		return {
			images: [],
			detail:""
		};
	},
	mounted() {
			this.detailCompany();
	},
	methods: {
		goto(rt){
			this.$router.push({
				path:"/"+rt
			})
		},
		detailCompany() {
			var that=this;
			this.$http.get('/company/detailCompany').then(function (res) {
				if (res.code==10000) {
					that.detail=res.data;
					that.images=res.data.img.split(',')
				}else{
					Toast.fail(res.msg);
				}
			}).catch(function () {
			});
		},
	}
}
</script>
<style lang="scss" scoped>
.cc-main{
	background:linear-gradient(149deg,rgba(67,88,90,1) 0%,rgba(16,76,84,1) 100%);
	padding:32px 0 60px 24px;
	color:#fff;
	position: relative;
	.cc-edit{
		position: absolute;
		top:26px;
		right: 0;
		padding:10px 24px 10px 10px;
		line-height: 20px;
		img{
			width: 14px;
			height: 13px;
			margin:3px 0 0 8px;
		}
	}
	.cc-name{
		font-size:22px;
		font-weight:500;
		line-height:30px;
		margin-bottom: 8px;
	}
	.cc-title{
		font-size:18px;
		font-weight:500;
		line-height:25px;
		margin-bottom: 16px;
	}
	.cc-content{
		color:rgba(255,255,255,0.85);
		line-height:20px;
		margin-bottom: 36px;
		padding-right: 24px;
	}
	.cc-pic{
		margin-bottom: 36px;
		.my-swipe{
			height: 124px;
			.cc-item{
				width: 200px;
				height: 124px;
				border-radius:8px;
				overflow: hidden;
			}
		}
	}
}
</style>